.container__card > .card {
  border: 3px solid #f1f1f1 !important;
  border-top-right-radius: 2rem !important;
  border-bottom-right-radius: 2rem !important;
  border-bottom-left-radius: 2rem !important;
  overflow: hidden !important;
  margin: 0 0.75rem;
}
.card__slides {
  width: 16rem;
  height: 20rem;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.slick-list > div > div > div > div {
  display: flex;
  justify-content: center;
  align-items: center;
}
.container__slider {
  display: grid;
  grid-template-columns: 70%;
  justify-content: center;
  background-color: #28475b;
  padding: 3rem 0;
  margin: 3rem 0;
}
.container__slider > div {
  padding: 1.3rem 0.75rem;
  border-radius: 0.75rem;
  /* box-shadow: 3px 2px 10px rgb(0, 0, 0, 0.75); */
}
.image__slide > img {
  width: 100%;
  height: 250px !important;
}

.prev_icon {
  position: absolute;
  top: 50%;
  left: -40px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  justify-items: center;
}
.prev_icon > button > svg {
  margin-left: 0.55rem;
}

.next_icon {
  position: absolute;
  top: 50%;
  right: -40px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  justify-items: center;
}
.next_icon > button > svg {
  margin-left: 0.15rem;
}
